@import "https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap";
body {
  color: #ccc;
  background-color: #292929;
  font-family: Roboto Mono, monospace;
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 50px;
  display: flex;
}

.content {
  margin: 10% auto auto;
}

.content h2 {
  text-align: center;
  font-size: 2.5em;
}

form {
  justify-content: center;
  align-items: center;
  display: flex;
}

form input {
  color: #ccc;
  background-color: #0000;
  border: none;
  border-right: .5px solid #000;
  outline: 0;
  width: 200px;
  height: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  font-family: Roboto Mono, monospace;
  box-shadow: -4px -2px 7px -2px #000000bf;
}

form input:not(:first-child) {
  margin-left: 30px;
}

button {
  color: #292929;
  background: red;
  border: none;
  outline: none;
  width: 120px;
  height: 30px;
  margin-left: 30px;
  font-family: Roboto Mono, monospace;
  font-size: 1em;
  font-weight: 700;
  box-shadow: -4px -2px 7px -2px #000000bf;
}

form input:focus, button:hover {
  transition: all .2s;
  transform: scale(1.2);
}

.main_header {
  text-align: center;
  font-size: 2.5em;
}

.basicInformationBlock {
  padding: 12px 12px 11px 15px;
  box-shadow: 25px 25px 25px #00000040, -25px 25px 25px #00000040;
}

.basicInformationBlock h1 {
  font-weight: 400;
}

.auditsInformationCharts {
  padding: 12px 12px 11px 15px;
  box-shadow: 25px 25px 25px #00000040, -25px 25px 25px #00000040;
}

.xpcount {
  width: 70%;
}

